<template>
   <div class="wrapper">
     <div class="cabinet" ref="cabinet">
        <img src="/img/mutant.png" v-show="vis" alt="mutant" class='mutant'>
        <img src="/img/camera.png" alt="camera" class="camera" @click='setSep'>
     </div>
    <button class="next3" @click="toNext" v-show="vis">Дальше</button>
   </div>
</template>
<script>
export default {
  data() {
    return {
       vis:false
    }
  },
  methods: {
    setSep () {
      let cab = this.$refs.cabinet;
      cab.style.filter =  "invert(120%)";
      this.vis = true;
    },
    toNext () {
      this.$router.push('/level5');
    }
  }
}
</script>
<style scoped>
  .wrapper {
    width: 600px;
    margin: 50px auto;
  }
 
  .cabinet {
    width:100%;
    height: 400px;
    background-image:url('/img/cabinet.jpg');
    background-repeat:no-repeat;
    background-size:100% 100%;
    position: relative;
  }
 
  .mutant {
    position: absolute;
    width: 200px;
    height: 300px;
    right:0;
    bottom:0;
  }

  .camera {
    width: 60px;
    left:50px;
    position: absolute;
    bottom:40px;
    cursor:pointer;
  }

  .next3 {
    font-family:horror;
    color:red;
    cursor: pointer;
    font-size:25px;
    display: block;
    text-align: center;
    width:120px;
    margin:20px auto;
  }

</style>


